export const totalClients = mockClients.length;
export const activeProjects = mockProjects.filter(p => p.status === 'In Progress').length;
export const newClientsThisMonth = mockClients.filter(c => {
    const today = new Date();
    const clientDate = new Date(c.cooperationDate);
    return clientDate.getMonth() === today.getMonth() && clientDate.getFullYear() === today.getFullYear();
}).length;
